.head{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.main{
    background-color: #ffffff;
    box-sizing: border-box;
    box-shadow: 2px 4px 12px 6px rgb(245, 240, 240);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 60vh;
    border-radius: 4px;
    width: 35vw;
}
.main_login{
    background-color: #ffffff;
    box-sizing: border-box;
    box-shadow: 2px 4px 12px 6px rgb(245, 240, 240);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 50vh;
    border-radius: 4px;
    width: 35vw;
}
@media screen and (max-width: 480px) {
  .main_login {
    width:auto;
  }
  input{
    width: 10px;
  }
}

.span{
    margin-bottom: 22px;
    color: rgb(255, 124, 124);
    cursor: pointer;
    font-weight: 500;
}
.spantext{
    font-size: smaller;
    font-weight: 500;
    margin-left: 40px;
    margin-right: 25px;
    margin-bottom: 15px;
}
input{
    width: auto;
    padding: 10px;
    margin: 10px;
    border: none;
    border-left: 2px solid rgb(232, 232, 232);
    border-right: 2px solid rgb(232, 232, 232);
    border-radius: 5px;
}




button{
    padding: 10px;
    border: none;
    background-color: rgb(255, 124, 124);
    border-radius: 4px;
    color: white;
    margin: 5px;
}
button:hover{
    color: rgb(0, 0, 0);
    cursor: pointer;
}

a{
    text-decoration: none;
    color: black;
    margin: 15px;
    font-size: small;
}

.forgot{
    margin-top: -8px;
    margin-bottom: 10px;
    font-size: small;
    color: blue;
    margin: 5px;
}

.error{
    color: red;
    font-size: small;
}